*{

font-family: 'Times New Roman', Times, serif, Courier, monospace;

}
body{
  color: rgb(0, 0, 0);
  background-color: #ffffff;
padding: 30px;
padding-left: 20px;
}

input {
color: rgb(0, 0, 0);
background-color: rgb(255, 255, 255);
width: 100%;

font-size: 1.2rem;

}

.card {
align-content: center;
border: 1px solid grey;
border-radius: 6px;
overflow: hidden;
margin-bottom:10px ;
margin-right: 10px;

}

.card p {
padding: 5px;
text-align: center;

}
.card img {
  max-height: 170px;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
